import { Link, Outlet } from "react-router-dom";
import css from "./About.module.css";

function About() {
    return (
        <>
            <Outlet></Outlet>
            <div></div>

            {/*  CSS import  module 
                import   css.abc     . */}
            <div className={css.abc}> </div>
            <Link to="/about"></Link>
            <Link to="member"></Link>
            <Link to="location"></Link>
            <Outlet>    </Outlet>
        </>
    );
}

export default About;
