import { configureStore, createSlice } from "@reduxjs/toolkit";

let user = createSlice({
    name: "user",
    initialState: { name: "kim", age: 20 },

    //  state 
    reducers: {
        changeName(state) {
            state.name = "park";
        },
        increaseAge(state, action) {
            // parameter  payload
            action = action.payload;

            state.age += action;
        },
        addName(state) {
            return [...state, "kim"];
        }
    }
});

// state    .   ~
export let { changeName, increaseAge } = user.actions;
export default user;
